* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  max-width: 400px;
  font-weight: 700;
  font-size: 18px;
}

.country-list>li {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 24px;
}

li:not(:last-child) {
  margin-bottom: 8px;
}

li>span {
  font-weight: 400;
}